.m_menu { position: absolute; top: 50%; right: 1rem; transform: translateY(-50%); width: 30px; height: 30px; z-index: 101; transition: 0.5s cubic-bezier(0.165, 0.84, 0.44, 1); cursor: pointer; display: none; }

