.m_menu .group div { position: absolute; width: 100%; height: 3px; background-color: #fff; border-radius: 5px; transition: all 0.5s ease; }

.m_menu .group .line1 { top: 3px; }

.m_menu .group .line2, .m_menu .group .line3 { top: 50%; margin-top: -1.5px; }

.m_menu .group .line3 { opacity: 0; }

